html {
  box-sizing: border-box;
  background-repeat: no-repeat;
  scroll-behavior: smooth;
  height: 100%;
  min-width: 15.625rem;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}

a {
  text-decoration: none;
