/* Slideshow container */
.carousel {
  position: relative;
  margin: auto;
  max-width: 31.25rem;
  height: 100%;
  overflow: hidden;
}
@media all and (min-width: 62em) {
  .carousel {
    max-width: 31.25rem;
    height: 100%;
  }
}

.r2-video {
  width: 31.25rem;
  height: 100%;
}

/* Hide the images by default */
.carousel__slide {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  transition: left 1s ease;
}

/* Show the current slide */
.carousel__slide.current-slide {
  left: 0;
  z-index: 7;
}

/* Move the previous slide off-screen to the left */
.carousel__slide.previous-slide {
  left: -100%;
}

/* Move the next slide off-screen to the right */
.carousel__slide.next-slide {
  left: 100%;
}

/* Cover element to hide previous slides */
.carousel__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 81px;
  height: 600px;
  background: white;
  z-index: 4;
}

.carousel__image {
  display: block;
  margin-inline: auto;
  width: 100%;
  height: auto;
  border-radius: 5px 5px 0 0;
}

/* // carousel text */
.carousel__image-description {
  margin-inline: auto;
  padding-block: 0.375rem;
  width: 100%;
  max-width: 500px;
  height: 30px;
  text-align: center;
  border-radius: 0 0 5px 5px;
}

/* //Next & previous buttons */
.carousel__prev-button,
.carousel__next-button {
  cursor: pointer;
  position: absolute;
  top: 45%;
  width: auto;
  font-weight: bold;
  font-size: 1rem;
  transition: 0.6s ease;
  user-select: none;
  border: 0;
  background-color: transparent;
  z-index: 163;
}

.carousel__prev-button {
  left: -4px;
}

.carousel__next-button {
  right: -4px;
}

