/* The dots indicators */
.carousel__bottom-control {
  margin-top: 630px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  z-index: 163;
}

.carousel__progress-list {
  display: flex;
  flex-direction: row;
  min-width: 110px;
}

.carousel__pause-button,
.carousel__play-button {
  border: none;
  background-color: transparent;
  display: block;
}

.carousel__pause-button.hidden,
.carousel__play-button.hidden {
  display: none;
}

.carousel__transition-button.disabled {
  pointer-events: none;
  opacity: 0.5;
}

.carousel__progress-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin: 0 5px;
  padding: 0;
}

.carousel__progress-bar {
  margin: 0;
  padding: 0;
  height: 0.75rem;
  width: 0.75rem;
  border-radius: 100%;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

.current-container {
  max-width: 3.5rem;
  width: 100%;
  height: 0.75rem;
  border-radius: 0.75rem;
}

.current-bar {
  width: 0%;
  border-radius: 0.75rem;
}

main {
  margin-inline: 3%;
}
@media all and (min-width: 48em) {
  main {
    width: 25rem;
    margin-inline: auto;
  }
}
@media all and (min-width: 62em) {
  main {
    width: 37.5rem;
  }
}
@media all and (min-width: 76.25em) {
  main {
    width: 50rem;
  }
}
@media all and (min-width: 87.5em) {
  main {
    width: 62.5rem;
  }
}

ul {
  list-style-image: url("../../src/assets/images/raw/arrow-right.png");
}

ul li {
  position: relative;
  margin-left: 30px;
  /* Adjust based on image size */
  margin-block: 0.3125rem;
  /* Adjust as needed */
}

h1 span {
  position: relative;
  /* Make parent element positioned relatively */
  padding-left: 15px;
  /* Add padding to avoid text overlap with the image */
}

h1 span::after {
  content: "";
  position: absolute;
  left: 5px;
  bottom: 0;
  width: 30px;
  /* Adjust to image width */
  height: 30px;
  /* Adjust to image height */
  background-image: url("../../src/assets/images/raw/tea-leaves.png");
  background-size: contain;
  /* Ensure image is fully contained */
  background-repeat: no-repeat;
  display: inline-block;
  /* Ensure the pseudo-element is displayed */
}

h2 span {
  position: relative;
  /* Make parent element positioned relatively */
  padding-left: 15px;
  /* Add padding to avoid text overlap with the image */
}

h2 span::after {
  content: "";
  position: absolute;
  left: 5px;
  bottom: 2px;
  width: 45px;
  /* Adjust to image width */
  height: 45px;
  /* Adjust to image height */
  background-image: url("../../src/assets/images/raw/leaf-64.png");
  background-size: contain;
  /* Ensure image is fully contained */
  background-repeat: no-repeat;
  display: inline-block;
  /* Ensure the pseudo-element is displayed */
}

h3,
.pen {
  position: relative;
  /* Make parent element positioned relatively */
  padding-left: 30px;
  /* Add padding to avoid text overlap with the image */
}

h3::before,
.pen::before {
  content: "";
  position: absolute;
  left: -5px;
  bottom: -5px;
  width: 40px;
  /* Adjust to image width */
  height: 40px;
  /* Adjust to image height */
  background-image: url("../../src/assets/images/raw/pen.png");
  background-size: contain;
  /* Ensure image is fully contained */
  background-repeat: no-repeat;
  display: inline-block;
  /* Ensure the pseudo-element is displayed */
}

